.card {
	margin-bottom: 24px;
	box-shadow: 0 0 .875rem 0 rgba(33, 37, 41, .05)
}

.card-header {
	border-bottom-width: 1px
}

.card-actions a {
	color: #495057;
	text-decoration: none
}

.card-actions svg {
	width: 18px;
	height: 18px
}

.card-actions .dropdown {
	line-height: 1.4
}

.card-title {
	font-size: .925rem;
	font-weight: 600;
	color: #939ba2
}

.card-subtitle {
	font-weight: 400
}

.card-table {
	margin-bottom: 0
}

.card-table tr td:first-child,
.card-table tr th:first-child {
	padding-left: 1.25rem
}

.card-table tr td:last-child,
.card-table tr th:last-child {
	padding-right: 1.25rem
}

.card-img,
.card-img-bottom,
.card-img-top {
	max-width: 100%;
	height: auto
}

@media (-ms-high-contrast:none) {
	.card-img,
	.card-img-bottom,
	.card-img-top {
		height: 100%
	}
}

.chart {
	margin: auto;
	position: relative;
	width: 100%;
	min-height: 300px
}

.chart-xs {
	min-height: 150px
}

.chart-sm {
	min-height: 250px
}

.chart-lg {
	min-height: 350px
}

.chart-xl {
	min-height: 500px
}

.chart canvas {
	max-width: 100%
}

.chat-online {
	color: #2bdfaa
}

.chat-offline {
	color: #e4606d
}

.chat-messages {
	display: flex;
	flex-direction: column;
	max-height: 800px;
	overflow-y: scroll
}

.chat-message-left,
.chat-message-right {
	display: flex;
	flex-shrink: 0
}

.chat-message-left {
	margin-right: auto
}

.chat-message-right {
	flex-direction: row-reverse;
	margin-left: auto
}

.content {
	padding: 1.5rem 1.5rem .75rem;
	flex: 1;
	width: 100vw;
	max-width: 100vw;
	direction: ltr
}

@media (min-width:768px) {
	.content {
		width: auto;
		max-width: auto
